@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
.customButton{width:100px;height:30px;font-size:18px;background:#fff;outline:none;border:1px solid #000;border-radius:8px;cursor:pointer;transition:.3s}.customButton:hover{background:#d3d2d2}.customButton:active{background:#fff}

.loginForm{position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);background:#fff;width:400px;height:220px;display:flex;flex-direction:column;justify-content:space-between;align-items:center;box-sizing:border-box;border:1px solid black;border-radius:8px}.loginForm__header{display:flex;justify-content:space-between;width:100%;box-sizing:border-box;padding:10px 20px;border-bottom:1px solid black}.loginForm__title{font-size:24px;text-align:left}.loginForm__exit{border:none;background:none;outline:none;cursor:pointer}.loginForm__content{width:100%;height:100%;display:flex;flex-direction:column;justify-content:space-between;align-items:center;box-sizing:border-box;padding:20px}.loginForm__input{width:70%;padding:5px 0;border:none;outline:none;background:none;border-bottom:1px solid black}

.navbar{position:fixed;top:0;display:flex;justify-content:space-between;align-items:center;width:100%;padding:10px 200px;box-sizing:border-box}.navbar__list{display:flex;padding:10px 0}.navbar__listItem{display:inline-block;margin:0 20px;font-size:18px}.navbar__link{text-decoration:none;color:#000;transition:.3s}.navbar__link:hover{color:#d3d2d2;border-bottom:1px solid #d3d2d2}.navbar__link:active,.navbar__link_active{color:#000;border-bottom:1px solid #000}

.homePage{width:100%;height:100%;display:flex;justify-content:center;align-items:center}.homePage__welcome{font-size:48px;text-align:center}

.newsArticle{width:100%;margin:20px 0}.newsArticle__title{display:inline-block;margin-bottom:5px}.newsArticle__date{margin-left:10px}.newsArticle__text{padding-left:10px}.newsArticle__controls{margin-top:10px}.newsArticle__approveText,.newsArticle__approveButton{margin-right:10px}

.newsForm__addButton{width:200px;margin-top:20px;align-self:end}.newsForm__form{display:flex;flex-direction:column;margin:10px 0}.newsForm__input,.newsForm__textarea{width:70%;padding:5px 0;border:none;outline:none;background:none;border-bottom:1px solid black;margin:10px 0}.newsForm__textarea{resize:none}.newsForm__submitButton{margin-top:20px}

.news{display:flex;flex-direction:column;padding:0 20%}.news__search{width:100%;font-size:18px;padding:5px 0;border:none;outline:none;background:none;border-bottom:1px solid black}

*{margin:0;padding:0;font-family:'Rubik'}body{width:100vw;height:100vh}#root,.App{width:100%;height:100%;padding-top:32px;box-sizing:border-box}

